.header {
  background: 
  // linear-gradient(
  //                 to top,
  //                 rgba(0, 0, 0, 0) 0%,
  //                 rgba(0, 0, 0, 1) 100%
  // ),
  url("https://assets2.razerzone.com/images/pnx.assets/05575b50e6ed9a1d9a5615c544f6bc63/holiday-gift-guide-rgb-homepage-large-2x.webp") no-repeat center center/cover;
  width: 100%;
  height: 91vh;
  border-bottom: 3px solid rgb(57, 56, 56);
}
.empty-div {
  text-align: center;
  padding: 20px 20px;
  background: #555;
  width: 100%;
  font-size: 17px;
  letter-spacing: 1px;
  height: 60px;
  color: #fff;
  //margin-top: 50px;
}

.bigger {
  font-size: 50px;
  letter-spacing: 3px;
  text-align: center;
  padding-top: 70px;
  font-family: "Razer F5 Medium";
}

.big {
  font-weight: 300;
  text-align: center;
  font-size: 30px;
}

.flexbox {
  display: flex;
  justify-content: center;
  gap: 30px;

  .header-btn {
    background: transparent;
    border: none;
    font-size: 25px;
    font-weight: bold;
    cursor: pointer;

    .arrow {
      color: #44d62c;
      font-size: 20px;
      font-weight: bold;
    }
  }
}

.flexbox .header-btn:hover {
  border-bottom: 2px solid white;
}

.arrow:hover {
  border-bottom: 1px solid white;
}


@media all and (min-width: 1280px) {
  .container {
    max-width: 1146px;
    margin: 0 auto;
  }
}

@media all and (max-width: 860px) {
  .empty-div {
    height: 80px;
    padding: 25px 20px !important;
  }
  .home {
    padding-top: 20px;
  }
}

@media all and (max-width: 768px) {
  .bigger {
    font-size: 35px;
  }
  .big {
    font-size: 20px;
  }
}

@media all and (max-width: 600px) {
  .empty-div {
    height: 70px;
    padding: 15px 20px !important;
  }
}

@media all and (max-width: 479px) {
  .empty-div {
    height: max-content;
  }
}

