.loadDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    top: 25%;
}
.load  {
    text-align: center;
    font-family: "Razer F5 Bold";
    border: 4px solid #fff;
    width: 33%;
    h1{
        font-size: 50px;
    }
}


@media all and (max-width:768px) {
    .load {
        border: none;
        width: 100%;
    }
}