.aside{
    background: url("https://assets2.razerzone.com/images/pnx.assets/d24672112ff237ece31fd792eb39dba6/razer-deathstalker-pro-tkl-homepage-large.jpg") no-repeat center center/cover;
    width: 100%;
    height: 91vh;
    border-bottom: 9px solid rgb(57, 56, 56);
}
.s2-bigger {
    font-size: 50px;
    text-align: center;
    font-family: "Razer F5 Medium";
    letter-spacing: 2px;
    padding-top: 60px;
}
.s2-big{
    font-weight: 300;
    text-align: center;
    font-size: 30px;
}
.flexbox{
    display: flex;
    justify-content: center;
    gap: 30px;
    button{
      background: transparent;
      border: none;
      font-size: 25px;
      font-weight: bold;
      cursor: pointer;
      .arrow{
        color: #44d62c;
        font-size: 20px;
        font-weight: bold;
      }
    }
  }
  .flexbox button:hover {
    border-bottom: 2px solid white;
}




@media all and (min-width: 1280px) {
    .container {
      max-width: 1146px;
      margin: 0 auto;
    }
  }

@media all and (max-width: 768px) {
  .s2-bigger {
    font-size: 35px;
    padding: 60px 0  20px;
  }
  .s2-big{
    font-size: 20px;
    padding: 0 20px;
  }
  .flexbox {
    margin-top: 30px;
    flex-direction: column;
    text-align: center;
  }
}