.mobile-content {
       background: #111;
       height: max-content;
       padding: 3rem 1rem;
       text-align: center;
       h1{
              font-size: 2.25rem;
              line-height: 2.25rem;
              font-weight: 400;
              color: #44d62c;
              padding-bottom: 1rem;
       }
       p {
              color: #999;
              font-size: 19px;
              font-family: "Razer F5 Regular";
       }
}
.mobile-head {
       background: url("https://assets2.razerzone.com/images/pnx.assets/91baf798ef1e551ef8d4e850c28a0050/razer-edge-1920x700-desktop-cat-banner.jpg") no-repeat center center/cover;
       width: 100%;
       height: 700px;
       .mobile-txt{
              text-align: center;
              padding: 3rem 20px;
       }
       h2{
              font-size: 28px;
              margin-bottom: 16px;
              letter-spacing: 2px;
       }
       p{
           font-size: 20px;
           font-family: "Razer F5 Regular";   
           color: #999;
           margin-bottom: 16px;
       }
       a{
              color: #44d62c;
              font-size: 18px;
              font-weight: 700;
       }
}
.m-gray-grid{
       width: 100%;
       height: max-content;
       padding: 2rem 0;
       background: #252525;
}
.mobile-grid {
       display: grid;
       grid-template-columns: repeat(3,1fr);
       gap: 20px;
       margin: 0 auto;
       padding: 0 20px;
}
.m-card1 {
     background: #111;  
     height: max-content;
     padding-bottom: 10px;
     border-bottom: 1px solid #44d62c;
     
     img{ 
       width: 100%;
       object-fit: cover;
     }
     h2{
       padding: 0 15px;
       font-size: 18px;
       font-weight: 400;
       font-family: "Razer F5 Regular";
     }
     p{
       min-height: 104px;
       padding: 10px 15px;
       font-size: 14px;
       color: #999;
       font-family: "Razer F5 Regular";
     }
     a {
       padding: 0 15px;
       color: #44d62c;
       font-family: "Razer F5 Regular";
     }
}

.mobile-sect {
       background: url("https://assets2.razerzone.com/images/pnx.assets/8d15cb8a565e71ef01f047785d22ce69/gothe-extramile-category-1920x700-desktop.jpg") no-repeat center center/cover;
       width: 100%;
       height: 89vh;
       border-bottom: 2px solid #44d62c;
}

.m-sect-content {
       text-align: center;
       h1{
              padding-top: 50px;
              font-size: 28px;
       }
       p {
              font-size: 18px;
              color: #999;
              font-family: "Razer F5 Regular";
              padding: 0 100px;
              line-height: 30px;
       }
       a{
              color: #44d62c;
              font-family: "Razer F5 Regular";
              line-height: 60px;
       }
}
.mobile-aside{
 padding: 30px 0;
 background: #252525;
}

.m-card1 h2{
       min-height: 44px;
}


@media all and (max-width:1100px) {
       .mobile-head {
              background: url("https://static.razer.ru/233871/razer-kishi-xbox-gallery-01.jpg") no-repeat center center/cover !important;
              width: 100%;
       }
       .mobile-grid {
              grid-template-columns: repeat(2,1fr);
       }
}

@media all and (max-width: 600px) {
       .mobile-grid {
              grid-template-columns: repeat(1,1fr);
       }
       .mobile-content  {
              h1{
                     font-size: 25px;
              }
              p{
                     font-size: 14px;
              }
       }
       .m-sect-content {
              text-align: center;
              h1{
                     padding-top: 50px;
                     font-size: 25px;
              }
              p {
                     font-size: 14px;
                     color: #999;
                     font-family: "Razer F5 Regular";
                     padding: 0 20px;
                     line-height: 30px;
              }
              a{
                     color: #44d62c;
                     font-family: "Razer F5 Regular";
                     line-height: 60px;
                     font-weight: bold;
                     
              }
       }
}